@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&family=Style+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Header{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
}
.section1{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 90px;
    width: 80%;
    background-color: #fff;
    color: black;
    padding: 0.4em;
}
.immiglobal-text{
    text-transform: uppercase;
    font-weight: 600;
    font-size: 2em;
    font-family: 'Dancing Script', cursive;
}
.immiglobal-text span{
    position: relative;
    left: 20px;
}
.contacting{
    display: flex;
    width: 50%;
    margin: 0;
    padding: 0;
}
ul{
    list-style: none;
}
.media-link{
    display: flex;
}
.media-link li{
    display: flex;
    align-items: center;
    background-color: yellow;
    padding: 0.4em;
    margin-right: 2px;
    font-size: 2.5em;
}
.contact-link{
    font-weight: 600;
    align-items: center;
    line-height: 40px;
}
.contact-link li{
    font-size: 1.4em;
    display: flex;
    align-items: center;
}
.contact-link li i{
    margin-right: 10px;
}
.section2{
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    width: 80%;
    height: 40px;
}
.hero{
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    background-color: #252526;
    height: 90vh;
    width: 100%;
    font-size: 3em;
    text-decoration: line-through;
}
.intro{
    overflow-x: hidden;
}
.intro1{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: black;
    margin-top: 10vh;
    padding-bottom: 10vh;
    background-color: #f5f5f5;

}
.intro1 h2{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 2.7em;
}
.intro1 h2::after{
    content: '';
    border: 2px solid #40aeed;
    background-color: #40aeed;
    width: 1.5em;
    height: 2px;
}
.intro-cards{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 72%;
}
.country-card{
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    background:url('https://wallpapercave.com/wp/wp1958398.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 49%;
    height: 35vh;
}
.country-card:nth-child(2){
    background: url('data:image/png;base64,');
    background-size: cover;
}
.intro2{
    width: 100%;
    color: black;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;
    margin-bottom: 20vh;
}
.services{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: black;
}
.services h2{
    font-size: 2.5em;
    margin: 0;
    margin-top: 10px;
}
.services p{
    color: grey;
}
.services-bar{
    width: 70%;
}
.services-bar ul{
    display: flex;
    align-items: center;
    justify-content: center;
}
.services-bar ul li{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-right: 2.6em;
    font-size: 1em;
    width: 20%;
}
.services-bar ul i{
    font-size: 5em;
}

.MidSection{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: black;
    margin-bottom: 100vh;
}
.display{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 1.5em;
    height: 60vh;
    width: 100%;
    background: url('https://wallpaper.dog/large/20382896.jpg');
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;
}
.display::before{
    content: '';
    position: absolute;
	bottom: 0;
	width: 100%;
	height: 100px;
	background: linear-gradient(to bottom, black, transparent);
	z-index: 1;
}
.display::after{
    content: '';
    position: absolute;
	bottom: 0;
	width: 100%;
	height: 100px;
	background: linear-gradient(to top, #0a2a43, transparent);
	z-index: 1000;
    mix-blend-mode: color;
}
.App {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif ;
  width: 100%;
  color: #fff;
  overflow: hidden;
  box-sizing: border-box;
}
div{
  border: 2px solid black;
}

